.detailsContainer {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.detailsContainer li {
    margin: 1rem 0;
    padding: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
    max-width: 800px;
    box-sizing: border-box;
}

.detailsContainer p {
    margin: 0.5rem 0;
}